import CreateButton from "../../../Ui/Button/CreateButton";
import style from "../LmsManager.module.css";
import { useTranslation } from "react-i18next";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { Link, useLocation, useParams } from "react-router-dom";
const LmsAllStaffCard = ({ staff, staffAlias, isInstitute }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const onNull = () => {};
  return (
    <div className={style.lms_staff_card}>
      <div className={style.lms_staff_card_contatiner}>
        <Link
          key={staff?._id}
          to={`/q/${params.username}/staff/profile`}
          state={{
            sid: staff?._id,
            viewAs: "DEPARTMENT_HEAD",
          }}
        >
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
        </Link>
        <div className={style.lms_staff_card_contatiner_inner}>
          <h6>
            {`${staff?.staffFirstName ?? ""} ${staff?.staffMiddleName ?? ""} ${
              staff?.staffLastName ?? ""
            }`}
          </h6>
          <p>
            {staffAlias ? staffAlias : t("index_number")}
            {": "}
            {staff?.staffROLLNO ?? ""}
          </p>
        </div>
      </div>
      <Link
        to={
          isInstitute
            ? `/q/${params.username}/institute/member/lms?type=staff-overview&category=All Leave`
            : `/q/${params.username}/member/staff/lms?type=staff-overview&category=All Leave`
        }
        state={{
          ...getQuery.state,
          staffId: staff?._id,
          staffName: `${staff?.staffFirstName ?? ""} ${
            staff?.staffMiddleName ?? ""
          } ${staff?.staffLastName ?? ""}`,
          urlOffSet: isInstitute ? "Institute" : "",
        }}
      >
        <CreateButton
          label={t("leave_overview")}
          customStyle={{
            margin: "0",
          }}
          onAction={onNull}
        />
      </Link>
    </div>
  );
};

export default LmsAllStaffCard;
