import { useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import LmsAllStaffCard from "./LmsAllStaffCard";
import { useInstituteSearchAllStaff } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const LmsAllStaff = ({ instituteId, staffAlias, isInstitute }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { instituteSearchAllStaff, instituteSearchAllStaffLoading } =
    useInstituteSearchAllStaff({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteSearchAllStaff?.list?.length) {
      let obj = nextPage(instituteSearchAllStaff?.list?.length);
      setState(() => obj);
    }
  }, [instituteSearchAllStaff?.list]);

  const onSearchEvent = (e) => {
    if (
      !instituteSearchAllStaffLoading &&
      instituteSearchAllStaff?.list?.length >= 0
    ) {
      setSearchCopy(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("all_staff")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  onKeyUp={onCallBindSearch}
                  value={searchCopy}
                />
              </div>
            </section>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        {instituteSearchAllStaff?.list?.map((staff, index) =>
          instituteSearchAllStaff?.list?.length === index + 1 ? (
            <div key={staff?._id} ref={ref}>
              <LmsAllStaffCard
                staff={staff}
                staffAlias={staffAlias}
                isInstitute={isInstitute}
              />
            </div>
          ) : (
            <LmsAllStaffCard
              staff={staff}
              key={staff?._id}
              staffAlias={staffAlias}
              isInstitute={isInstitute}
            />
          )
        )}
        {instituteSearchAllStaffLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          instituteSearchAllStaff?.list?.length === 0 && (
            <EmptyMessage title="No staff found related this search." />
          )
        ) : (
          instituteSearchAllStaff?.list?.length === 0 && (
            <EmptyMessage title="No staff." />
          )
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsAllStaff;
